@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Noto Sans TC";
  margin: 0 auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.selector-for-some-widget {
  box-sizing: content-box;
}

html {
  height: 100%;
}

img {
  width: 100%;
  vertical-align: middle;
}

.logoImg {
  margin-bottom: 16px;
}

.vhContainer {
  height: 100vh;
}

.bg-yellow {
  background-color: #FFD370;
}

.bg-half {
  background-image: linear-gradient(175deg, #FFD370 60%, #fff 40%);
}

.conatiner {
  margin: 0 auto;
  padding: 87px 32px;
}

@media (max-width: 576px) {
  .conatiner {
    padding: 18px 32px;
  }
}

.side {
  width: 386px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media (max-width: 576px) {
  .side {
    width: 100%;
  }
}

nav {
  display: flex;
  justify-content: space-between;
  padding: 24px 32px 0 32px;
}

@media (max-width: 576px) {
  nav {
    margin-bottom: 16px;
  }
}

nav h1 a {
  width: 243px;
  height: 39px;
  background: url(https://upload.cc/i1/2022/03/23/8vTzYG.png) no-repeat;
  display: block;
  text-indent: 101%;
  overflow: hidden;
  white-space: nowrap;
}

nav ul {
  display: flex;
  font-size: 1rem;
}

@media (max-width: 576px) {
  nav ul li {
    margin-top: 11px;
  }
}

nav ul a {
  text-decoration: none;
  color: #333333;
  margin-left: 24px;
}

@media (max-width: 576px) {
  nav ul a {
    margin-left: 0;
  }
}

nav ul a:hover {
  color: #d87355;
}

nav ul a span {
  font-weight: bold;
}

@media (max-width: 576px) {
  nav ul .todo_sm {
    display: none;
  }
}

@media (max-width: 576px) {
  .d-m-n {
    display: none;
  }
}

.formControls {
  margin-left: 100px;
  display: flex;
  flex-direction: column;
}

@media (max-width: 576px) {
  .formControls {
    margin-left: 0;
  }
}

.formControls .formControls_txt {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 24px;
}

@media (max-width: 576px) {
  .formControls .formControls_txt {
    font-size: 1.25rem;
    text-align: center;
  }
}

.formControls .formControls_label {
  font-size: 0.875rem;
  font-weight: bold;
  margin: 16px 0 4px 0;
}

.formControls .formControls_input {
  font-weight: normal;
  border: none;
  border-radius: 10px;
  width: 304px;
  padding: 12px 16px;
  margin: 4px 0;
}

.formControls .formControls_input:focus {
  outline: 3px solid #fff;
}

.formControls .formControls_input::-webkit-input-placeholder {
  color: #9F9A91;
}

.formControls .formControls_input::placeholder {
  color: #9F9A91;
}

.formControls .formControls_btnSubmit {
  width: 128px;
  height: 48px;
  border: none;
  border-radius: 10px;
  background: #333333;
  color: #fff;
  align-self: center;
  margin: 24px 0;
  font-weight: bold;
  cursor: pointer;
  text-align: center;
  font-size: 1rem;
}

.formControls a {
  text-decoration: none;
}

.formControls span {
  margin: 4px 0 16px 0;
  color: #d87355;
  font-size: 0.875rem;
}

.formControls .formControls_btnLink {
  display: block;
  color: #333333;
  font-weight: bold;
  text-decoration: none;
  text-align: center;
}

.loginPage {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 800px;
}

@media (max-width: 576px) {
  .loginPage {
    width: 100%;
    flex-direction: column;
    margin: 0 auto;
    padding: 48px 31px;
    justify-content: start;
  }
}

.signUpPage {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 800px;
}

@media (max-width: 576px) {
  .signUpPage {
    width: 100%;
    flex-direction: column;
    margin: 48px auto 0 auto;
    padding: 48px 31px;
  }
}

.todoListPage {
  padding: 16px 32px;
}

@media (max-width: 576px) {
  .todoListPage {
    background-image: linear-gradient(175deg, #FFD370 100%, #fff 0%);
  }
}

.todoList_Content {
  width: 500px;
  margin: 0 auto;
}

@media (max-width: 576px) {
  .todoList_Content {
    width: 100%;
  }
}

.inputBox {
  width: 100%;
  display: flex;
  position: relative;
  margin-bottom: 16px;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.15);
}

.inputBox input {
  background: #fff;
  border: none;
  border-radius: 10px;
  position: relative;
  width: 100%;
  height: 47px;
  font-size: 1rem;
  padding-left: 16px;
}

.inputBox a {
  display: block;
  width: 40px;
  height: 39px;
  position: absolute;
  background: #333333;
  color: white;
  font-size: 20px;
  text-decoration: none;
  text-align: center;
  border-radius: 10px;
  top: 4px;
  right: 4px;
  padding: 10px;
}

.todoList_list {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.15);
}

.todoList_list .todoList_tab {
  display: flex;
  justify-content: space-evenly;
}

.todoList_list .todoList_tab li {
  width: 100%;
}

.todoList_list .todoList_tab a {
  display: block;
  color: #9F9A91;
  text-decoration: none;
  line-height: 20px;
  font-weight: bold;
  text-align: center;
  padding: 16px;
  border-bottom: 2px solid #efefef;
}

.todoList_list .todoList_tab .active {
  color: #333333;
  border-bottom: 2px solid #333333;
}

.todoList_list .todoList_items {
  padding-top: 23px;
  padding-left: 24px;
  padding-right: 17px;
  padding-bottom: 32px;
}

.todoList_list .todoList_items .todoList_item {
  margin-bottom: 8px;
}

.todoList_list .todoList_items .todoList_label {
  width: 100%;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #e5e5e5;
  padding-bottom: 15px;
  color: #333333;
  line-height: 20.27px;
}

.todoList_list .todoList_items .todoList_input {
  width: 20px;
  height: 20px;
  border: 1px solid #9F9A91;
  border-radius: 5px;
  margin-right: 16px;
}

.todoList_list .todoList_items .todoList_input:checked~span {
  color: #9F9A91;
  text-decoration: line-through;
  transition: all 0.4s ease-in-out;
}

.todoList_list .todoList_items li {
  display: flex;
  align-items: center;
  margin-bottom: 17px;
}

.todoList_list .todoList_items li a {
  margin-left: 17px;
  display: block;
  font-size: 14px;
  color: #333333;
  opacity: 0;
}

.todoList_list .todoList_items li:hover a {
  opacity: 1;
}

.todoList_list .todoList_statistics {
  display: flex;
  justify-content: space-between;
}

.todoList_list .todoList_statistics p {
  color: #333333;
  font-size: 0.875rem;
}

.todoList_list .todoList_statistics a {
  color: #9F9A91;
  font-size: 0.875rem;
  text-decoration: none;
}
body {
  background: url(data:image/gif;base64,) repeat;
}

/*通用樣式*/
.u_flex {
  display: flex;
}

.u_flex--center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.u_flex--column {
  flex-direction: column;
}

.u_flex--row {
  flex-direction: row;
}

.active-93 p {
  color: #934961;
}

.largeA-w {
  color: #ffffff;
  font-size: 26px;
  font-weight: 900;
  letter-spacing: 7px;
  line-height: 1.3em;
}

.container {
  max-width: 70vw;
  margin: 8px auto;
  /* padding: 0 12px; */
  text-decoration: none
}

.container a {
  text-decoration: none;
}

/*  login*/


.login-link {
  display: flex;
  background-color: #ebcf00;
  margin-bottom: 4px;
  height: 95px;
  padding: 10px;
  justify-content: space-between;
  text-decoration: none
}

.login-link img {
  width: 270px;
  margin-top: 8px;
  margin-left: 12px;
}

.login-link a {
  text-decoration: none;
}

/* nav */

.nav-link {
  display: flex;
  height: 75px;
  background: #C4C0B1;
  padding: 10px 16px;
}


.nav-link a {
  display: inline-block;
  width: 120px;
  margin-right: 12px;
  text-align: center;
  /* background-color: #396c96; */
  color: white;
  text-decoration: none
}

/* .nav-link a.active {
  color: #934961;
} */

.nav-link a:hover,
.nav-link a:focus,
.nav-link a:active {
  color: #fff;
}

/* 產品列表 */
.product-list-wrapper {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-gap: 16px;
  list-style: none;
  padding: 0;
}

.product-list-wrapper li {
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
  border-radius: 10px;
  overflow: hidden;
  transition-duration: .3s;
}

.product-list-wrapper li:hover {
  opacity: 0.5;
}

.product-list-wrapper li img {
  display: block;
  width: 100%;
  height: 120px;
}

.product-list-wrapper li a {
  text-decoration: none;
}

.product-list-wrapper li p {
  color: #333;
  font-weight: bold;
}

/* home about */
.home-about {
  height: auto;
  background-color: #ebcf00;
  margin-bottom: 4px;
  padding: 20px 5px;
  font-weight: 900;
  font-size: 14px;

}

.home-about a {
  text-decoration: none;
}

.home-search {
  height: auto;
  background-color: #ebcf00;
  margin-bottom: 4px;
  padding-top: 10px;
  padding-right: 30px;
  padding-bottom: 10px;
  padding-left: 20px;
  font-weight: 900;
  font-size: 14px;
  color: #444444;
  text-decoration: none;
}

.contact {
  height: auto;
  background-color: #C4C0B1;
  padding: 8px 24px;
  display: flex;
  justify-content: flex-end;
}

.contact img {
  height: 30px;
  width: auto;
  margin: 0 1rem;
}

.rssNews {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 8px;
  list-style: none;
  padding: 0;
  font-size: 12px;
}

.rssNews img {
  height: 100px;
}

.footer {
  height: auto;
  background: #ebcf00;
  color: #3f2504;
  font-size: 8px;
  padding: 8px 24px;
  margin-top: 3px;
}

/* rwd */
@media (max-width: 1024px) {
  .product-list-wrapper {
    grid-template-columns: repeat(6, 1fr);
  }

  .rssNews {
    grid-template-columns: repeat(5, 1fr);
  }

  .container {
    max-width: 80vw;
  }
}

@media (max-width: 768px) {
  .product-list-wrapper {
    grid-template-columns: repeat(5, 1fr);
  }

  .rssNews {
    grid-template-columns: repeat(4, 1fr);
  }

  .container {
    max-width: 100vw;
  }
}

@media (max-width: 640px) {
  .product-list-wrapper {
    grid-template-columns: repeat(3, 1fr);
  }

  .rssNews {
    grid-template-columns: repeat(3, 1fr);
  }

  .largeA-w {
    font-size: 20px;
  }

  .login-link img {
    width: 200px;
    margin: 8px 4px;
  }

  .footer {
    background-color: #fff;
    color: #000;
  }
}
